// 
export function sortYearMonths(date1, date2) {
  // 
  function compareYearMonth(a, b) {
    // 
    a = a.replace('-', '');
    b = b.replace('-', '');

    // 
    var aYear = parseInt(a.substring(0, 4));
    var aMonth = parseInt(a.substring(4));
    var bYear = parseInt(b.substring(0, 4));
    var bMonth = parseInt(b.substring(4));

    // 
    if (aYear !== bYear) {
      return aYear - bYear;
    } else {
      // 
      return aMonth - bMonth;
    }
  }

  // 
  return compareYearMonth(date1, date2);
}
// //
// //
export function selectDataFromArr(returndata, zbCode, fieldKey, dbCode = 'nd', cityCode = '', yearLimit = 10) {

  // 
  const allYears = returndata.dataList.data[dbCode]
    .map(d => parseInt(d.date?.substring(0, 4), 10))
    .filter(y => !isNaN(y));
  const maxDataYear = Math.max(...allYears);

  //  maxDataYear 
  const filteredData = returndata.dataList.data[dbCode]
    .filter(returnDataObj => {
      //  zbCode  cityCode 
      const isMatchZb = returnDataObj.code.search(zbCode) !== -1;
      const isMatchCity = cityCode === '' || returnDataObj.cityCode.search(cityCode) !== -1;
      if (!isMatchZb || !isMatchCity) return false;

      //  yearLimit 
      if (yearLimit !== null && yearLimit > 0) {
        const year = parseInt(returnDataObj.date?.substring(0, 4), 10);
        if (isNaN(year)) return false;
        return year >= maxDataYear - yearLimit + 1;
      }

      return true;
    })
    .sort((a, b) => {
      // 
      return sortYearMonths(a.date, b.date);
    })
    .filter((returnDataObj, index, array) => {
      // 
      let filteredData = array.filter(item => item.value !== '');

      //  0 
      let lastNonZeroIndex = filteredData.length - 1;
      for (let i = filteredData.length - 1; i >= 0; i--) {
        if (filteredData[i].value !== 0) {
          lastNonZeroIndex = i;
          break;
        }
      }

      //  0 0
      return index <= lastNonZeroIndex || returnDataObj.value !== 0;
    })
    .map(item => {
      // 
      return item[fieldKey];
    });

  return filteredData;
}

// 
export function getCommonChartOption(params) {
  
  // 
  const {
    data,
    title,
    subtitle,
    zbcodeArr,
    cityCodeArr = [],
    dbCode = 'nd',
    unit = '',
    exceptName = '',
    legendTop,
    gridTop,
    chartType = 'bar',
    yearLimit,
    isHorizontal = false,
    legendAllSelected
  } = params;

  // 
  const fullYears = (data.dataList.sj?.[dbCode] || []).sort((a, b) => a.localeCompare(b));
  const filteredYears = yearLimit ? fullYears.slice(-yearLimit) : fullYears;

  // 
  const seriesData = [];


  if (cityCodeArr.length === 0) {
    // 
    zbcodeArr.forEach(zbCode => {

      let originalCname = selectDataFromArr(data, zbCode, 'cname', dbCode, '', yearLimit)?.[0] || '';
      let cname = originalCname;
    
      //  cname  exceptName 
      if (typeof cname === 'string' && typeof exceptName === 'string') {
        const resultArr = cname.split('');
        const exceptArr = exceptName.split('');
        exceptArr.forEach(ch => {
          const idx = resultArr.indexOf(ch);
          if (idx !== -1) resultArr.splice(idx, 1);
        });
        cname = resultArr.join('').trim() || '';
      }

      const name = cname + unit;
      const valueArr = selectDataFromArr(data, zbCode, 'value', dbCode, '', yearLimit) || [];
      console.log('yearLimit---valueArr', yearLimit,valueArr);

      seriesData.push({
        name: name,
        type: chartType,
        data: valueArr,
      });
    });
  } else {
    // 
    cityCodeArr.forEach(cityCode => {
      const city = data.dataList.reg?.find(r => r.code === cityCode);
      const name = city?.cname || '';
      const valueArr = selectDataFromArr(data, zbcodeArr[0], 'value', dbCode, cityCode, yearLimit) || [];

      seriesData.push({
        name: name,
        type: chartType,
        data: valueArr,
      });
    });
  }
  // logger.debug('',seriesData)
  // 
  const valueAxisConfig = {
    type: 'value',
    scale: true,
    min: (value) => value.min - (value.max - value.min) * 0.1, // 10%
    max: (value) => value.max + (value.max - value.min) * 0.1,
    axisLabel: {
      formatter: (value) => value.toFixed(2) + unit,
    },
  };

  // 
  const categoryAxisConfig = {
    type: 'category',
    data: filteredYears,
  };

  return {
    title: {
      text: title,
      subtext: subtitle,
      left: 'center',
      top: '15',
      itemGap: 15, 
      subtextStyle: {
        fontWeight: 'bold',
        fontSize: 13,
        lineHeight: 15,
        width: window.innerWidth * 0.8,
        overflowWrap: 'break-word', // 
      },
    },
    tooltip: {
      trigger: 'axis',
    },
    legend: {
      type: 'scroll',
      left: 'center',
      top: legendTop,
      data: seriesData.map(s => s.name),
      selected: legendAllSelected ? seriesData.reduce((acc, s) => ({ ...acc, [s.name]: true }), {}) : {},
    },
    grid: {
      left: '1%',
      right: '1%',
      top: gridTop,
      bottom: '1%',
      containLabel: true,
    },
    xAxis: isHorizontal ? valueAxisConfig : categoryAxisConfig,
    yAxis: isHorizontal ? categoryAxisConfig : valueAxisConfig,
    series: seriesData
    
  };

}


